@charset "utf-8";

//  Symbols  Progress
// --------------------------------------------------
.t-applys-progress {
  display: flex;
  flex-flow: column;
  align-items: flex-end;

  .t-progress-wrap {
    width: 100%;
    height: .5rem;
    border-radius: $border-radius-sm;
    overflow: hidden;
    background-color: $grey-50;
    margin: .5rem 0;

    .t-progress-bar {
      display: block;
      height: inherit;
      border-radius: $border-radius-sm;
      width: 0;
      background-color: $primary;
    }
  }

  .t-applys-txt {
    font-size: 0.875rem;

    strong {
      color: $primary-600;
      font-size: 125%;
      margin-right: .5rem;
    }
  }
}