*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: 16px;
}
html.js-font-scale-up-1st {
  font-size: 17px;
}
html.js-font-scale-up-2nd {
  font-size: 18px;
}
html.js-font-scale-up-3rd {
  font-size: 19px;
}
html.js-font-scale-up-4th {
  font-size: 20px;
}
html.js-font-scale-down-1st {
  font-size: 15px;
}
html.js-font-scale-down-2nd {
  font-size: 14px;
}
html.js-font-scale-down-3rd {
  font-size: 13px;
}
html.js-font-scale-down-4th {
  font-size: 12px;
}
@media (max-width: 1200px) {
  html {
    font-size: 18px;
  }
}
@media (max-width: 992px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}
@media (max-width: 576px) {
  html {
    font-size: 13px;
  }
}

body {
  background-color: var(--tu-body-bg, #F5F5F6);
  color: var(--tu-body-color, #121328);
  font-family: "halyard-display", "source-han-sans-traditional", system-ui, -apple-system, BlinkMacSystemFont, segoe ui, Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif, apple color emoji, segoe ui emoji, segoe ui symbol;
  font-style: normal;
  line-height: 1.5;
  margin: 0;
  height: 100%;
  min-height: 100%;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

pre,
code,
kbd,
samp {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 0.8125rem;
  margin: 0.5rem 0;
}

a {
  cursor: pointer;
  color: #4667CC;
  text-decoration: none;
  transition: all 0.25s ease-in-out;
}
a:hover {
  color: #2e4ca7;
  text-decoration: underline;
}

a[href^=http]:empty:before {
  content: attr(href);
}

figure,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
strong {
  font-weight: 700;
}

b,
h4,
h5,
h6 {
  font-weight: 500;
}

dl,
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

dd {
  margin-inline-start: 0;
  margin-left: 0;
}

table {
  width: 100%;
  background-color: transparent;
  border-collapse: collapse;
}

button,
input {
  border-color: transparent;
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
input,
optgroup,
select,
textarea {
  border-color: transparent;
  outline-color: transparent;
  font-family: "halyard-display", "source-han-sans-traditional", system-ui, -apple-system, BlinkMacSystemFont, segoe ui, Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif, apple color emoji, segoe ui emoji, segoe ui symbol;
  font-size: 100%;
  line-height: 1.5;
  margin: 0;
  outline: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35rem 0.75rem 0.625rem;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

image,
img,
svg {
  border-style: none;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

[data-unit-before]:before,
[data-unit-after]:after {
  display: inherit;
  color: inherit;
  font-size: 85%;
  font-weight: inherit;
}

[data-unit-after]:after {
  content: attr(data-unit-after);
  margin-left: 0.125rem;
}

[data-unit-before]:before {
  content: attr(data-unit-before);
  margin-right: 0.125rem;
}

::selection {
  color: #4667CC;
  background-color: rgba(70, 103, 204, 0.12);
}

h1 {
  font-size: 2.75rem;
}

h2 {
  font-size: 2.25rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.3125rem;
}

h5 {
  font-size: 1.125rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1rem;
}

span {
  font-size: 0.875rem;
}

label {
  font-size: 0.9375rem;
}

b {
  font-weight: 500;
}

srtong {
  font-weight: 700;
}

em {
  font-style: italic;
}

small {
  font-size: 75%;
}

big {
  font-size: 125%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25rem;
}

sup {
  top: -0.5rem;
}

address {
  padding: 0.5rem 0.75rem;
}

time {
  opacity: 0.8;
}

cite {
  font-size: 125%;
  font-weight: 500;
}

s:before, s:after {
  clip-path: inset(100%);
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
s:before:before, s:after:before {
  content: " [start of stricken text] ";
}
s:before:after, s:after:after {
  content: " [end of stricken text] ";
}

data {
  opacity: 0.8;
}

hr {
  margin: 1rem 0;
  border: 0;
  color: var(--tu-grey-400, #ababb3);
  background-color: currentcolor;
  height: 0.0625rem;
  max-width: 100%;
  background-position: 50%;
  /* <hr data-hr-style="dashed" data-hr-width="2">  */
  /* <hr data-hr-style="twill" data-hr-width="2">  */
  /* <hr data-hr-style="dotted" data-hr-width="2">  */
  /* <hr data-hr-style="fade" data-hr-width="2"> */
  /* <hr data-hr-style="border-fade" data-hr-width="2"> */
}
hr:before, hr:after {
  content: "";
}
hr[data-hr-width="2"] {
  height: 0.125rem;
}
hr[data-hr-width="3"] {
  height: 0.1875rem;
}
hr[data-hr-width="4"] {
  height: 0.25rem;
}
hr[data-hr-style=dashed] {
  background: repeating-linear-gradient(to right, currentcolor 0px, currentcolor 4px, transparent 0px, transparent 10px);
}
hr[data-hr-style=twill] {
  padding: 0.25rem;
  background: repeating-linear-gradient(135deg, currentcolor 0px, currentcolor 1px, transparent 1px, transparent 6px);
}
hr[data-hr-style=dotted] {
  background: radial-gradient(currentcolor 2px, transparent 2px);
  background-size: 6px 4px;
  padding: 0.125rem;
}
hr[data-hr-title] {
  position: relative;
  background-color: inherit;
  overflow: visible;
}
hr[data-hr-title]:before {
  content: attr(data-hr-title);
