.t-link {
  color: #1BC5BD;
  display: inline-block;
}
.t-link:visited {
  color: #1BC5BD;
}

.t-link--highlight-from-left {
  position: relative;
  display: inline-block;
}
.t-link--highlight-from-left:after {
  content: "";
  position: absolute;
  left: -4px;
  top: -4px;
  width: 0;
  height: calc(100% + 8px);
  background-color: #4667CC;
  transition: width 0.6s ease;
  mix-blend-mode: multiply;
}
.t-link--highlight-from-left:hover::after, .t-link--highlight-from-left:focus::after {
  width: calc(100% + 8px);
}
.t-link--highlight-from-right {
  position: relative;
  display: inline-block;
}
.t-link--highlight-from-right:after {
  content: "";
  position: absolute;
  right: -4px;
  top: -4px;
  width: 0;
  height: calc(100% + 8px);
  background-color: #4667CC;
  transition: width 0.6s ease;
  mix-blend-mode: multiply;
}
.t-link--highlight-from-right:hover::after, .t-link--highlight-from-right:focus::after {
  width: calc(100% + 8px);
}
.t-link--highlight-from-bottom {
  position: relative;
  display: inline-block;
}
.t-link--highlight-from-bottom:after {
  content: "";
  position: absolute;
  left: -4px;
  bottom: -4px;
  width: calc(100% + 8px);
  height: 0%;
  background-color: #4667CC;
  transition: height 0.6s ease;
  mix-blend-mode: multiply;
}
.t-link--highlight-from-bottom:hover::after, .t-link--highlight-from-bottom:focus::after {
  height: calc(100% + 8px);
}
.t-link--highlight-from-top {
  position: relative;
  display: inline-block;
}
.t-link--highlight-from-top:after {
  content: "";
  position: absolute;
  left: -4px;
  top: -4px;
  width: calc(100% + 8px);
  height: 0%;
  background-color: #4667CC;
  transition: height 0.6s ease;
  mix-blend-mode: multiply;
}
.t-link--highlight-from-top:hover::after, .t-link--highlight-from-top:focus::after {
  height: calc(100% + 8px);
}

.t-link--highlight-fade-in {
  padding: 1px 0;
  background-color: transparent;
  transition: background-color 0.6s ease-in-out, color 0.6s;
}
.t-link--highlight-fade-in:hover, .t-link--highlight-fade-in:active {
  background-color: #4667CC;
  color: black;
}
.t-link--highlight-from-strikethrough {
  padding: 1px 0;
  background-image: linear-gradient(transparent 0%, transparent calc(50% - 0.1ex), #4667CC calc(50% - 0.1ex), #4667CC calc(50% + 0.5ex), transparent calc(50% + 0.5ex), transparent 100%);
  background-color: transparent;
  transition: background-color 0.6s ease-in-out, color 0.6s;
}
.t-link--highlight-from-strikethrough:hover, .t-link--highlight-from-strikethrough:active {
  background-color: #4667CC;
  color: black;
}
.t-link--highlight-from-bottom-border {
  padding: 1px 0;
  text-decoration: none;
  background-image: linear-gradient(transparent 0%, transparent calc(50% - 0.4ex), #4667CC calc(50% - 0.4ex), #4667CC 100%);
  background-size: 100% 200%;
  background-position: 0 0;
  transition: background-position 0.6s ease-in-out, color 0.6s;
}
.t-link--highlight-from-bottom-border:hover, .t-link--highlight-from-bottom-border:active {
  background-position: 0 100%;
  color: black;
}

.t-link--crossing-triangle-waves {
  padding: 2px;
  background-size: 20px auto, 20px auto;
  background-repeat: repeat-x, repeat-x;
  background-position: 0 2em, 0 4em;
  background-image: conic-gradient(from 65deg at 0% 70%, #072ac8 65deg 180deg, transparent 180deg), conic-gradient(from -65deg at 100% 40%, transparent 180deg, #1e96fc 180deg);
  color: #1BC5BD;
