  color: #1BC5BD;
  color: transparent;
  -webkit-text-stroke: 1px #1BC5BD;
  background-image: linear-gradient(transparent 0%, transparent calc(50% - 0.4ex), #fcf300 calc(50% - 0.4ex), #fcf300 100%);
  background-size: 100% 200%;
  background-position: 0 0;
}
.t-link--fill-up-transform__clip {
  display: block;
  transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1);
  overflow: hidden;
  position: absolute;
  top: 0;
  transform: translateY(100%);
  pointer-events: none;
}
.t-link--fill-up-transform__filled {
  display: block;
  transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1);
  color: #1BC5BD;
  transform: translateY(-100%);
}
.t-link--fill-up-transform:hover .t-link--fill-up-transform__clip, .t-link--fill-up-transform:hover .t-link--fill-up-transform__filled {
  transform: translateY(0);
}

.t-link--split-in-half {
  position: relative;
  display: inline-block;
  line-height: 1em;
  font-size: 2em;
  /*Can't use this type of underscore for multiple lines of text :( 
  Good for menu buttons though, this would use a transform animation instead of a background position animation, better performance :)
  .t-link--bottom:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 8%;
    background: red;
    transform-origin: left bottom;
    transition: transform 0.2s ease;
  }

  .t-link:hover .t-link--bottom:before {
    transform: scaleX(0%);
  }
  */
}
.t-link--split-in-half--top {
  position: absolute;
  color: #1BC5BD;
  top: 0;
  display: inline-block;
  clip-path: polygon(0% 66%, 0% 0%, 100% 0%, 100% 40%);
  transition: transform 0.6s cubic-bezier(0.16, 1, 0.3, 1) 0.25s;
}
.t-link--split-in-half--top:after {
  content: "";
  position: absolute;
  top: 36%;
  left: 0;
  width: 100%;
  height: 8%;
  background: #1e96fc;
  transform: rotateZ(-2.2deg) scaleX(0%);
  transform-origin: right top;
  transition: transform 0.2s ease 0.22s;
}
.t-link--split-in-half--bottom {
  display: inline-block;
  clip-path: polygon(0% 65%, 100% 40%, 100% 110%, 0% 110%);
  text-decoration: underline;
  color: #1BC5BD;
  transition: background-position 0.2s ease 0.22s;
  text-decoration: none;
  background-size: 200% 8%;
  background-position: left bottom;
  background-repeat: no-repeat;
  background-image: linear-gradient(to right, #1e96fc 50%, transparent 50%);
}
.t-link--split-in-half--bottom:after {
  content: "";
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  height: 8%;
  background: #1e96fc;
  transform: rotateZ(-2deg) scaleX(0%);
  transform-origin: right top;
  transition: transform 0.2s ease 0.22s;
}
.t-link--split-in-half:hover .t-link--split-in-half--top {
  transform: translateY(-0.5em) rotateZ(-3deg);
  transition: transform 0.5s cubic-bezier(0.12, 0.8, 0.57, 1) 0.42s;
}
.t-link--split-in-half:hover .t-link--split-in-half--bottom {
  background-position: 100% bottom;
  transition: background-position 0.2s ease;
}
.t-link--split-in-half:hover .t-link--split-in-half--top:after {
  top: 56.5%;
  transform-origin: left top;
  transform: rotateZ(-2.4deg) scaleX(100%);
}
.t-link--split-in-half:hover .t-link--split-in-half--bottom:after {
  top: 65%;
  transform-origin: left top;
  transform: rotateZ(-2.4deg) scaleX(100%);
}

