.pulse-ring {
  display: block;
  border-radius: 2.5rem;
  height: 2.5rem;
  width: 2.5rem;
  position: absolute;
  animation: animation-pulse 3.5s ease-out;
  animation-iteration-count: infinite;
  opacity: 0;
  border-width: 3px;
  border-style: solid;
  border-color: #9d9da6;
}

