.toggle {
  background: #ffffff;
  border-radius: 4px;
  border: rgba(60, 60, 60, 0.125) 1px solid;
  display: inline-block;
  height: 1.875rem;
  margin-right: 0.5rem;
  vertical-align: middle;
  width: 5.25rem;
}
.toggle .toggle-checkbox {
  cursor: pointer;
  margin: 0;
  opacity: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
}
.toggle .not-allowed {
  cursor: not-allowed;
}
.toggle .knobs,
.toggle .layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
