//
// Components -> Tabs
// --------------------------------------------------
.nav {
  margin-bottom: .5rem;
  padding-left: 0;
  list-style: none;

  @include clearfix;

  .nav-item {
    position: relative;
    display: block;

    [data-toggle="tab"] {
      position: relative;
      display: block;
      font-size: 1rem;
      cursor: pointer;
      padding: $nav-link-padding;
      transition: $transition-tabs;

      font {
        font-size: inherit;
      }

      i {
        color: $grey-500;
        font-size: 1.125rem;

        +font {
          margin-left: .375rem;
        }
      }

      >font,
      >i {
        display: inline-block;
        vertical-align: middle;
      }

      .badge {
        margin-left: .375rem;
      }

      &.active {
        @include title;

        i {
          color: $info;
        }

        // &,
        // &:hover,
        // &:focus {
        //   background-color: $nav-link-hover-bg;
        //   border-color: $link-color;
        // }
      }

      &:hover,
      &:focus {
        text-decoration: none;
        background-color: $nav-link-hover-bg;
      }

      &.disabled {
        color: $nav-disabled-link-color;

        &:hover,
        &:focus {
          color: $nav-disabled-link-hover-color;
          text-decoration: none;
          background-color: transparent;
          cursor: $cursor-disabled;
        }
      }
    }
  }

  .nav-divider {
    @include nav-divider;
  }

  .nav-item [data-toggle="tab"] img {
    max-width: none;
  }
}

.nav-grab {
  cursor: grab !important;
  // padding-left: 2rem;
  // padding-right: 2rem;

  &.nav-tabs {
    >li {
      >[data-toggle="tab"] {
        cursor: grab;
      }

      &.active {
        [data-toggle="tab"] {
          cursor: grab;
        }
      }
    }
  }
}

// nav tabs
// -------------------------
.nav-tabs {
  margin-bottom: 0.75rem;
  white-space: nowrap;
  width: 100%;
  border-bottom: 2px solid $primary;

  .nav-item {
    display: inline-block;
    vertical-align: bottom;
    margin-right: -1.5rem;
    padding: 0 1rem;

    &:first-child {
      padding-left: 0;

      [data-toggle="tab"] {
        &:before {
          display: none;
        }
      }
    }

    &:last-child {
      padding-right: 1rem;

      [data-toggle="tab"] {
        &:after {
          right: -1.125rem;
        }
      }
    }

    [data-toggle="tab"] {
      background-color: $lighter;
      border: 0 solid transparent;
      border-radius: $border-radius-large $border-radius-large 0 0;
      padding-left: 2rem;
      padding-right: 2rem;
      margin-bottom: 0;
      margin-right: .125rem;
      user-select: none;
      transition: none;

      &:before,
      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        width: 1.25rem;
        height: 1.25rem;
        background: $default;
        transition: none;
      }

      &:before {
        left: -1.25rem;
        background: radial-gradient(circle at 0 0, transparent 1.25rem, $default 1.375rem);
      }

      &:after {
        right: -1.25rem;
        background: radial-gradient(circle at 100% 0, transparent 1.25rem, $default 1.375rem);
      }

      &:hover {
        background: darken($default, 5%);

        &:before,
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          width: 1.25rem;
          height: 1.25rem;
          background: darken($default, 5%);
        }

        &:before {
          background: radial-gradient(circle at 0 0, transparent 1.25rem, darken($default, 5%) 1.375rem);
        }

        &:after {
          background: radial-gradient(circle at 100% 0, transparent 1.25rem, darken($default, 5%) 1.375rem);
        }
      }

      &.active {

        z-index: 1;

        &,
        &:hover,
        &:focus {
          color: $white;
          background-color: darken($primary, 0%);
          border-bottom-color: transparent;

          &:before {
            background: radial-gradient(circle at 0 0, transparent 1.25rem, darken($primary, 0%) 1.375rem);
          }

          &:after {
            background: radial-gradient(circle at 100% 0, transparent 1.25rem, darken($primary, 0%) 1.375rem);
          }
        }
      }
    }
  }

  &.nav-justified {
    @extend .nav-justified;
    @extend .nav-tabs-justified;
  }
}

// segment
// -------------------------
.nav-segment {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  border-radius: $border-radius-base;
  font-size: 0;
  padding: 0.375rem 0.375rem;
  background-color: $default;

  &[data-scroll="x"] {
    width: auto;

    .nav-item {
      white-space: initial;
    }
  }

  .nav-item {
    display: inline-block;
    vertical-align: middle;
    white-space: nowrap;

    &:not(:last-child) {
      margin-right: .25rem;
    }

    [data-toggle="tab"] {

      border-radius: $border-radius-base;
      color: $grey;
      margin-bottom: 0;
      user-select: none;

      &:hover {
        background-color: rgba($white, 0.36);
        color: $primary-700;
      }

      &.active {
        color: $dark;
        background-color: $white;
        box-shadow: $shadow-tab;

        .badge {
          border: 1px solid rgba($white, .48);
          box-shadow: $box-shadow-xs;
        }
      }
    }
  }
}

// Pills
// -------------------------
.nav-pills {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-bottom: .75rem;
  font-size: 0;

  .nav-item {
    display: inline-block;
    vertical-align: middle;

    &:not(:last-child) {
      margin-right: .5rem;
    }

    [data-toggle="tab"] {
      background-color: $default;
      border-radius: $border-radius-large;
      user-select: none;

      &:hover {
        background-color: lighten($default, 12%);
        color: $primary-700;
      }

      &.active {
        color: $white;

        &,
        &:hover,
        &:focus {
          color: $nav-pills-active-link-hover-color;
          background-color: $nav-pills-active-link-hover-bg;
        }
      }

      &:not(.active):hover {
        color: $primary-700;
        background-color: darken($default, 8%);
      }
    }
  }
}

// 堆疊
.nav-stacked {

  >li {
    float: none;

    +li {
      margin-bottom: .5rem;
      margin-left: 0;
    }
  }
}

// 
.nav-justified {
  margin: 0;
  width: 100%;
  white-space: initial;

  >li {

    // 判斷數量 2
    &:nth-last-child(2):first-child,
    &:nth-last-child(2):first-child~li {
      width: calc(50% - .125rem);

      @media (max-width: $screen-sm-min) {
        margin: 0;
        width: 100%;
      }
    }

    // 判斷數量 3
    &:nth-last-child(3):first-child,
    &:nth-last-child(3):first-child~li {
      width: calc(33.3333333% - 0.1666666rem);

      @media (max-width: $screen-sm-min) {
        margin: 0;
        width: 100%;
      }
    }

    // 判斷數量 4
    &:nth-last-child(4):first-child,
    &:nth-last-child(4):first-child~li {
      width: calc(25% - 0.1875rem);

      @media (max-width: $screen-sm-min) {
        margin: 0;
        width: 100%;
      }
    }

    // 判斷數量 5
    &:nth-last-child(5):first-child,
    &:nth-last-child(5):first-child~li {
      width: calc(20% - 0.25rem);

      @media (max-width: $screen-sm-min) {
        margin: 0;
        width: 100%;
      }
    }


    @media (max-width: $screen-sm-min) {
      float: none;
      padding: 0;
      width: 100%;
    }

    :not(:last-child) {
      >a {
        @media (max-width: $screen-sm-min) {
          margin-bottom: .75rem;
        }
      }
    }

    >a {
      text-align: center;
    }
  }

  >.dropdown .dropdown-menu {
    top: auto;
    left: auto;
  }

  // @media (min-width: $screen-sm-min) {
  //   >li {
  //     display: table-cell;
  //     width: 1%;

  //     >a {
  //       margin-bottom: 0;
  //     }
  //   }
  // }
}

// Mixin for adding on top the shared `.nav-justified` styles for our tabs
.nav-tabs-justified {
  border-bottom: 0;

  >li>a {
    // Override margin from .nav-tabs
    margin-right: 0;
    border-radius: $border-radius-base;
  }

  >.active>a,
  >.active>a:hover,
  >.active>a:focus {
    border: 0 solid $border;
  }

  @media (min-width: $screen-sm-min) {
    >li>a {
      border-bottom: 0 solid $border;
      border-radius: $border-radius-base $border-radius-base 0 0;
    }

    >.active>a,
    >.active>a:hover,
    >.active>a:focus {
      border-bottom-color: $primary;
    }
  }
}

// Dropdowns
// -------------------------
.nav-tabs .dropdown-menu {
  margin-top: -1px;

  @include border-top-radius(0);
}

// Tabs Transitions
// -------------------------
.tab-content {

  &:not(.tab-fade):not(.tab-sliding) {

    >.tab-pane {
      display: none;
    }

    >.active {
      display: block;
    }
  }

  >.active {
    padding: 1rem 0;
  }

  &.tab-fade {
    .tab-pane {
      opacity: 0;
      visibility: hidden;
      height: 0;
      transition: opacity 1.5s ease;
    }

    .active {
      opacity: 1;
      visibility: visible;
      height: auto;
    }
  }

  &.tab-sliding {
    position: relative;
    display: inline-block;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    font-size: 0;

    .tab-pane {
      max-width: 100%;
      display: inline-block;
      vertical-align: top;
      width: 100%;
      transform: none;
      visibility: hidden;
      max-height: 0;
      transition: margin-left, margin-right, transform, max-height, visibility, -webkit-transform;
      transition-duration: 350ms, 350ms, 350ms, 350ms, 0s;
      transition-delay: 0s, 0s, 0s, 0s, 340ms;

      &:not(.active) {
        pointer-events: none;
      }
    }

    .tab-pane {
      margin-left: -100%;

      &.active {
        transform: translateX(100%);
        visibility: visible;
        max-height: 100%;
        transition-delay: 0s;

        ~.tab-pane {
          margin-left: 100%;
          margin-right: -200%;
        }
      }
    }
  }
}
.tab-content {
  >.tab-pane {
    display: none;
  }

  >.active {
    display: block;
  }
}

.tab--icon {
