$card-bg-v1: #F6ABC8 !default;
$card-bg-v2: #FFC199 !default;
$card-bg-v3: #6EBEAF !default;
$card-bg-v4: #92BBFC !default;

$card-img-w: 8rem !default;
$card-img-border: 0.25rem dotted rgba($white, 0.66) !default;
