$card-bgs: () !default;
$card-bgs: map-merge(("v1": $card-bg-v1, "v2": $card-bg-v2, "v3": $card-bg-v3, "v4": $card-bg-v4), $card-bgs);
$card-computs: "v1" $card-bg-v1 $card-bg-v1-icon, "v2" $card-bg-v2 $card-bg-v2-icon, "v3" $card-bg-v3 $card-bg-v3-icon, "v4" $card-bg-v4 $card-bg-v4-icon;

// Alternate styles
@each $name, $value, $icon in $card-computs {
  .t-card {
    &.t-card-#{$name} {
      background-color: tint-color($value, 80);
      box-shadow: 0 2.75rem 5.5rem -3.5rem rgba($value, 0.28), 0 2rem 4rem -2rem rgba($value, 0.38);
      border: tint-color($value, 72) 0.125rem solid;

      .t-card-img {
        background-color: $value;
        border-radius: unquote("#{randomNums(10, 60)}%");

        &::before {
          transform: rotate(unquote("#{randomNums(-70, 70)}+deg"));
          left: unquote("#{randomNums(-2, 2)}+rem");
          top: unquote("#{randomNums(-2, 2)}+rem");
        }

        .t-card-img-icons {
          background-image: $icon;
        }
      }

      &:hover {
        box-shadow: 0 3rem 6rem -4.5rem rgba($value, 0.66), 0 2.5rem 4.25rem -1.5rem rgba($value, 0.52);

        .t-card-title {
          color: darken($value, 10%);
        }
      }
    }
  }
}

.t-card {
  // position: relative;

  .t-card-img {
    position: absolute;
    width: $card-img-w;
    height: $card-img-w;
    border: $card-img-border;

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      border: inherit;
      border-radius: inherit;
      background-color: inherit;
      opacity: 0.36;
    }

    .t-card-img-icons {
      opacity: 0.92;
      position: relative;
      z-index: 1;
    }
  }

  &:hover {
    .t-card-title {
      // font-size: 1.75rem;
      // transform: scale(1.05);
      // transition: $transition-base;
    }
  }

  .t-card-body {
    .t-card-list {
      .t-card-list-item {
        color: $grey;
        font-size: 0.875rem;

        &:not(:last-child) {
          margin-bottom: .5rem;
        }
      }
    }

    .t-card-title {
      position: relative;
      // margin-bottom: 1rem;

      // padding-bottom: 1rem;
   
    }
  }
}

.t-card {
  // scss-docs-start card-css-vars
  --#{$prefix}card-spacer-y: #{$card-spacer-y};
  --#{$prefix}card-spacer-x: #{$card-spacer-x};
  --#{$prefix}card-title-spacer-y: #{$card-title-spacer-y};
  --#{$prefix}card-border-width: #{$card-border-width};
  --#{$prefix}card-border-color: #{$card-border-color};
  --#{$prefix}card-border-radius: #{$card-border-radius};
  --#{$prefix}card-box-shadow: #{$card-box-shadow};
  --#{$prefix}card-inner-border-radius: #{$card-inner-border-radius};
  --#{$prefix}card-cap-padding-y: #{$card-cap-padding-y};
  --#{$prefix}card-cap-padding-x: #{$card-cap-padding-x};
  --#{$prefix}card-cap-bg: #{$card-cap-bg};
  --#{$prefix}card-cap-color: #{$card-cap-color};
  --#{$prefix}card-height: #{$card-height};
  --#{$prefix}card-color: #{$card-color};
  --#{$prefix}card-bg: #{$card-bg};
  --#{$prefix}card-img-overlay-padding: #{$card-img-overlay-padding};
  --#{$prefix}card-group-margin: #{$card-group-margin};

  // scss-docs-end card-css-vars

  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;

  // See https://github.t-com/twbs/bootstrap/pull/22740#issuecomment-305868106
  height: var(--#{$prefix}card-height);
  word-wrap: break-word;
  background-color: var(--#{$prefix}card-bg);
  background-clip: border-box;
  border: var(--#{$prefix}card-border-width) solid var(--#{$prefix}card-border-color);

  @include border-radius(var(--#{$prefix}card-border-radius));

  box-shadow: var(--#{$prefix}card-box-shadow);

  > hr {
    margin-right: 0;
    margin-left: 0;
  }

  > .list-group {
    border-top: inherit;
    border-bottom: inherit;

    &:first-child {
      border-top-width: 0;

      @include border-top-radius(var(--#{$prefix}card-inner-border-radius));
    }

    &:last-child {
      border-bottom-width: 0;

      @include border-bottom-radius(var(--#{$prefix}card-inner-border-radius));
    }
  }

  // Due to specificity of the above selector (`.t-card > .list-group`), we must
  // use a child selector here to prevent double borders.
  > .t-card-header + .list-group,
  > .list-group + .t-card-footer {
    border-top: 0;
  }
}

.t-card-body {
  // Enable `flex-grow: 1` for decks and groups so that card blocks take up
  // as much space as possible, ensuring footers are aligned to the bottom.
  flex: 1 1 auto;
  padding: var(--#{$prefix}card-spacer-y) var(--#{$prefix}card-spacer-x);
  color: var(--#{$prefix}card-color);
}

// .t-card-title {
//   margin-bottom: var(--#{$prefix}card-title-spacer-y);
// }

.t-card-subtitle {
  margin-top: calc(-.5 * var(--#{$prefix}card-title-spacer-y));
  margin-bottom: 0;
}

.t-card-text:last-child {
  margin-bottom: 0;
}

.t-card-link {
  &:hover {
    text-decoration: if($link-hover-decoration == underline, none, null);
  }

  + .t-card-link {
    margin-left: var(--#{$prefix}card-spacer-x);
  }
}

//
// Optional textual caps
//

.t-card-header {
  padding: var(--#{$prefix}card-cap-padding-y) var(--#{$prefix}card-cap-padding-x);
  margin-bottom: 0;

  // Removes the default margin-bottom of <hN>
  color: var(--#{$prefix}card-cap-color);
  background-color: var(--#{$prefix}card-cap-bg);
  border-bottom: var(--#{$prefix}card-border-width) solid var(--#{$prefix}card-border-color);

  &:first-child {
    @include border-radius(var(--#{$prefix}card-inner-border-radius) var(--#{$prefix}card-inner-border-radius) 0 0);
  }
}

.t-card-footer {
  padding: var(--#{$prefix}card-cap-padding-y) var(--#{$prefix}card-cap-padding-x);
  color: var(--#{$prefix}card-cap-color);
  background-color: var(--#{$prefix}card-cap-bg);
  border-top: var(--#{$prefix}card-border-width) solid var(--#{$prefix}card-border-color);

  &:last-child {
    @include border-radius(0 0 var(--#{$prefix}card-inner-border-radius) var(--#{$prefix}card-inner-border-radius));
  }
}

//
// Header navs
//

.t-card-header-tabs {
  margin-right: calc(-.5 * var(--#{$prefix}card-cap-padding-x));

  // stylelint-disable-line function-disallowed-list
  margin-bottom: calc(-1 * var(--#{$prefix}card-cap-padding-y));

  // stylelint-disable-line function-disallowed-list
  margin-left: calc(-.5 * var(--#{$prefix}card-cap-padding-x));

  // stylelint-disable-line function-disallowed-list
  border-bottom: 0;

  .nav-link.active {
    background-color: var(--#{$prefix}card-bg);
    border-bottom-color: var(--#{$prefix}card-bg);
  }
}

.t-card-header-pills {
  margin-right: calc(-.5 * var(--#{$prefix}card-cap-padding-x));

  // stylelint-disable-line function-disallowed-list
  margin-left: calc(-.5 * var(--#{$prefix}card-cap-padding-x));

  // stylelint-disable-line function-disallowed-list
}

// Card image
.t-card-img-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: var(--#{$prefix}card-img-overlay-padding);

  @include border-radius(var(--#{$prefix}card-inner-border-radius));
}

.t-card-img,
.t-card-img-top,
.t-card-img-bottom {
  width: 100%;

  // Required because we use flexbox and this inherently applies align-self: stretch
}

.t-card-img,
.t-card-img-top {
  @include border-top-radius(var(--#{$prefix}card-inner-border-radius));
}

.t-card-img,
.t-card-img-bottom {
  @include border-bottom-radius(var(--#{$prefix}card-inner-border-radius));
}

//
// Card groups
//

.t-card-group {
  // The child selector allows nested `.t-card` within `.t-card-group`
  // to display properly.
  > .t-card {
    margin-bottom: var(--#{$prefix}card-group-margin);
  }

  @include media-breakpoint-up(sm) {
    display: flex;
    flex-flow: row wrap;

    // The child selector allows nested `.t-card` within `.t-card-group`
    // to display properly.
    > .t-card {
      // Flexbugs #4: https://github.t-com/philipwalton/flexbugs#flexbug-4
      flex: 1 0 0%;
      margin-bottom: 0;

      + .t-card {
        margin-left: 0;
        border-left: 0;
      }

      // Handle rounded corners
      @if $enable-rounded {
        &:not(:last-child) {
          @include border-end-radius(0);

          .t-card-img-top,
          .t-card-header {
            // stylelint-disable-next-line property-disallowed-list
            border-top-right-radius: 0;
          }

          .t-card-img-bottom,
          .t-card-footer {
            // stylelint-disable-next-line property-disallowed-list
            border-bottom-right-radius: 0;
          }
        }

        &:not(:first-child) {
          @include border-start-radius(0);

          .t-card-img-top,
          .t-card-header {
            // stylelint-disable-next-line property-disallowed-list
            border-top-left-radius: 0;
          }

          .t-card-img-bottom,
          .t-card-footer {
            // stylelint-disable-next-line property-disallowed-list
            border-bottom-left-radius: 0;
          }
        }
      }
    }
  }
}