@charset "utf-8";

//==  Prototypes -> Size
//====================================================

@for $index from 0 through $grid-columns {
  .w-col-#{$index * 1} {
    width: percentage(($index / $grid-columns));
  }

  %w-col-#{$index * 1} {
    width: percentage(($index / $grid-columns));
  }
}

@for $index from 0 through 10 {
  .svg-w-#{$index * 10} {
    width: percentage(($index / 10));

    path {
      width: percentage(($index / 10));
    }
  }

  %svg-w-#{$index * 10} {
    width: percentage(($index / 10));

    path {
      width: percentage(($index / 10));
    }
  }
}

@for $index from 0 through 10 {
  .svg-h-#{$index * 10} {
    height: percentage(($index * 1));

    path {
      height: percentage(($index * 1));
    }
  }

  %svg-h-#{$index * 10} {
    height: percentage(($index* 1));

    path {
      height: percentage(($index * 1));
    }
  }
}

@for $i from 1 through 20 {
  .w-#{$i * 4} {
    width: unquote("#{$i * 1}rem !important");
  }

  %w-#{$i * 4} {
    width: unquote("#{$i * 1}rem");
  }
}

@for $i from 1 through 20 {
  .h-#{$i * 4} {
    height: unquote("#{$i * 1}rem !important");
  }

  %h-#{$i * 4} {
    height: unquote("#{$i * 1}rem");
  }
}





.symbol {
  display: inline-block;
  flex-shrink: 0;
  position: relative;
  border-radius: .95rem;
}

.symbol .symbol-label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  color: #3f4254;
  background-color: #f5f8fa;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  border-radius: .95rem;
}

.symbol .symbol-badge {
  position: absolute;
  border: 2px solid $white;
  border-radius: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%) translateY(-50%) !important;
}

.symbol > img {
  width: 100%;
  flex-shrink: 0;
  display: inline-block;
  border-radius: .95rem;
}

.symbol.symbol-square, .symbol.symbol-square .symbol-label, .symbol.symbol-square > img {
  border-radius: 0 !important;
}

.symbol.symbol-circle, .symbol.symbol-circle .symbol-label, .symbol.symbol-circle > img {
  border-radius: 50%;
}

.symbol > img {
  width: 50px;
  height: 50px;
}

