@charset "utf-8";

//   Components -> Buttons -> Buttons shadows
// ––––––––––––––––––––––––––––––––––––––––––––––––––––––––– */

// Buttons Shadow Colors
@each $color,
$value in $element-colors {
  .btn.btn--shadow--#{$color} {
    @include button-boxShadow-variant($value, $value);
  }
}
